body {
  height: 2000px; }

.item {
  overflow: hidden; }

img {
  display: block;
  width: 100%; }

.infinite-scroll {
  width: 1100px;
  margin: auto; }

@-webkit-keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0); }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none; } }

@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0); }
  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none; } }

.infinite-scroll__image {
  /* width: 200px;
  height: 200px; */
  margin: 5px;
  /* display: inline-block; */ }

.infinite-scroll__image.active {
  -webkit-animation: fadeInUp 1s forwards;
          animation: fadeInUp 1s forwards; }

.infinite-scroll__loading {
  width: 100%;
  height: 100px;
  line-height: 100px;
  margin: 5px;
  text-align: center;
  background-color: #666; }

.infinite-scroll__loading:before {
  content: 'Loading';
  font-family: Arial;
  color: #fff; }



